                                                    
export class TreeInspector {
                                                                     
  public readonly attributes: { [key: string]: any } = {};

                                                                                                                                                                                                                                                                             
  public addAttribute(key: string, value: any) {
    this.attributes[key] = value;
  }
}

                                                                        
export interface IInspectable {
                                                                                                                 
  inspect(inspector: TreeInspector): void;
}
